import { OPEN_MODAL, CLOSE_MODAL } from '../actions';

const initialState =  {
    selectedGif: null,
    modalIsOpen: false
};

export default function modal(state = initialState, action) {
    switch(action.type) {
        case OPEN_MODAL:
            //console.log(action)
            return Object.assign({},state,{
                modalIsOpen: true,
                selectedGif: action.gif.selectedGif
            })
        case CLOSE_MODAL:
            return Object.assign({},state,{
                modalIsOpen: false,
                selectedGif: null
            })
        default:
            return state;
    }
}

